.header{

    background:var(--background);
    border-bottom:1px solid var(--border);

    position: fixed;
    top:0;
    width: 100%;
    

}

.header .conteiner{

    display:flex;
    align-items:center;
    justify-content: space-between;

    height:60px;
    max-width: 957px;
    margin:0 auto;
    width: 90%;

}

.header .conteiner .logo{

    width: 110px;
    cursor: pointer;
}

.input-fake{

    background: var(--background-main);
    border-radius: 3px;

    border:1px solid var(--border);
    padding: 3px 10px;

    display:flex;
    align-items: center;


}
.input-fake input{

    font-size: 14px;
    padding: 0 7px;

}
.menu-icons{
    display:flex;
    gap: 16px;
    cursor: pointer;
    align-items: center;
}
.menu-icons .user{
    width: 26px;
    height: 26px;
    border-radius: 50%;

}