.conteiner-su{
    margin-top: 18px;
}
.header-sugge{
    display:flex;
    align-items:center;
    gap:20px;

}

.header-sugge img{
    height: 56px;
    width: 56px;
    border-radius: 100%;
    cursor:pointer;

}
.user-sugge{
    display:flex;
    align-items:center;
    justify-content:space-between;
    flex:1;

}

.info-sugge span{

    font-size: 15px;
    font-weight: 600;
    cursor:pointer;


}
 .info-sugge p {

    font-size: 13px;
    color:var(--secondary);

}
.trocar{

    font-weight: 500;
    color:var(--quartary);
    cursor:pointer;
}

.header-main-sugge {

   display:flex;
   justify-content: space-between;

   margin:20px 0 10px 0;

}

.header-main-sugge p{

    font-size: 15px;
    color:var(--secondary);

    font-weight: 500;
}
.header-main-sugge span{

font-size:13px;
font-weight: 500;
cursor:pointer;

}

.su{

    margin-top: 5px;

}

.su{

    margin: 15px 0 0 5px;
    display:flex;
    align-items:center;
    gap: 16px;
}
.su img{

    width:36px;
    height: 36px;
    border-radius:100%;
    cursor:pointer;

}

.su {
    flex:1;
}

.su span{

    font-size: 15px;
    font-weight: 600;
    cursor:pointer;
}
.su p{

    font-size: 15px;
    margin-top:13px;
    color:var(--secondary)
}
.follow{

    font-size:12px;
    cursor:pointer;
    color:var(--quartary);
    font-weight:500;

}

.footer{

    margin-top:25px;

    flex:1;

}

.footer p{
    font-size: 12px;
    color:var(--tertiary);

    margin-bottom:15px;
    cursor:pointer;
}




